/* src/pages/notice/NoticeCreate.module.css */

.container {
    max-width: 1440px;
    width: 60%;
    margin: 50px auto;
    padding: 30px;
    background: #ffffff;
    border: 2px solid #e0e0e0;
    border-radius: 15px;
    font-family: 'Arial', sans-serif;
  }
  
  /* 헤더 스타일 */
  .header {
    text-align: center;
    margin-bottom: 40px;
    font-size: 32px;
    color: #333333;
    font-weight: bold;
    letter-spacing: 1.5px;
  }
  
  /* 폼 스타일 */
  .form {
    display: flex;
    flex-direction: column;
    gap: 25px;
  }
  
  /* 테이블 스타일 */
  .table {
    width: 100%;
    border-spacing: 15px;
    margin-bottom: 20px;
  }
  
  .table th {
    width: 120px;
    text-align: left;
    font-size: 18px;
    color: #555;
    background-color: white;
    font-weight: bold;
    text-align: center;
  }
  
  .table td input[type="text"],
  .table textarea {
    width: 100%;
    padding: 12px;
    border: 1px solid #ddd;
    border-radius: 10px;
    font-size: 16px;
    transition: border-color 0.3s ease, box-shadow 0.3s ease;
    background-color: #fafafa;
  }
  
  .table td input[type="text"]:focus,
  .table td textarea:focus {
    border-color: #4a90e2;
    box-shadow: 0 0 8px rgba(74, 144, 226, 0.2);
    background-color: #ffffff;
    outline: none;
  }
  
  /* textarea 기본 스타일 */
  .textarea {
    resize: none;
    height: 300px;
  }
  
  /* 파일 입력 스타일 */
  .table input[type="file"] {
    font-size: 16px;
    padding: 10px;
    background-color: #f9f9f9;
    border: 1px solid #ddd;
    border-radius: 8px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .table input[type="file"]:hover {
    background-color: #f0f0f0;
  }
  
  /* 버튼 그룹 스타일 */
  .buttongroup {
    display: flex;
    flex-direction: row; /* 버튼들을 가로로 나열 */
    justify-content: center; /* 버튼들을 가운데 정렬 */
    border-radius: 12px;
    gap: 10px; /* 버튼 간의 간격 */
    width: 100%; /* 부모 요소의 너비를 100%로 설정 */
  }
  
  /* 버튼 스타일 (공통) */
  .submit,
  .reset,
  .back {
    margin: 20px 15px;
    padding: 12px 5px;
    font-size: 16px;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    font-weight: bold;
    transition: background-color 0.3s ease, box-shadow 0.3s ease;
    width: 130px;
    text-align: center;
  }
  
  /* 개별 버튼 색상 설정 */
  .submit {
    background-color: #4a90e2;
    color: white;
  }
  
  .submit:hover {
    background-color: #007bff;
    box-shadow: 0 4px 10px rgba(0, 123, 255, 0.2);
  }
  
  .reset {
    background-color: #FFB84D;
    color: white;
  }
  
  .reset:hover {
    background-color: #FFB84D;
    box-shadow: 0 4px 10px rgba(225, 75, 75, 0.2);
  }
  
  .back {
    background-color: #6c757d;
    color: white;
  }
  
  .back:hover {
    background-color: #5a6268;
    box-shadow: 0 4px 10px rgba(90, 98, 104, 0.2);
  }
  
  /* 반응형 스타일 */
  @media screen and (max-width: 768px) {
    .container {
      margin: 20px;
      padding: 15px;
    }
  
    .header {
      font-size: 24px;
    }
  
    /* 버튼 크기 조정 (모바일에서) */
    .submit,
    .reset,
    .back {
      font-size: 14px;
    }
  
    .buttongroup {
      flex-direction: column; /* 모바일에서는 버튼들을 세로로 나열 */
    }
  
    .buttongroup button {
      width: 100%; /* 모바일에서 버튼들을 가로폭에 맞춰 100%로 */
      margin-bottom: 10px; /* 버튼 간 간격 */
    }
  
    .submitbutton {
      border-radius: 10px !important;
    }
  }
  